import { useState, useRef, useEffect } from "react";
import { useDispatch } from "react-redux";
import { useNavigate, Link } from "react-router-dom";
import {
  Input,
  Button,
} from "@ya.praktikum/react-developer-burger-ui-components";
import styles from "./universal-styles-registraton.module.css";
import { setNewPasswordAction } from "../../services/actions/reset-password";

export default function ResetPassword() {
  const dispatch = useDispatch();
  const [value, setValue] = useState({
    passwordValue: "",
    codeValue: "",
  });
  const inputRef = useRef(null);
  const onIconClick = () => {
    setTimeout(() => inputRef.current.focus(), 0);
    alert("Icon Click Callback");
  };

  const recoverPassword = (e) => {
    e.preventDefault();
    dispatch(setNewPasswordAction(value));
  };
  //   
  const navigate = useNavigate();
  useEffect(() => {
    const flag = localStorage.getItem("flag");
    if (!flag) {
      navigate("/forgot-password");
    }
  }, [navigate]);

  return (
    <div className={styles.container}>
      <h1 className="text text_type_main-medium mb-6"> </h1>
      <form
        className={`${styles.form} text text_type_main-default`}
        onSubmit={(e) => recoverPassword(e)}
      >
        <Input
          type={"text"}
          placeholder={"  "}
          onChange={(e) =>
            setValue({ ...value, passwordValue: e.target.value })
          }
          icon={"false"}
          value={value.passwordValue}
          name={"name"}
          error={false}
          ref={inputRef}
          onIconClick={onIconClick}
          errorText={""}
          size={"default"}
          extraClass="mb-6"
        />
        <Input
          type={"text"}
          placeholder={"   "}
          onChange={(e) => setValue({ ...value, codeValue: e.target.value })}
          icon={"false"}
          value={value.codeValue}
          name={"name"}
          error={false}
          ref={inputRef}
          onIconClick={onIconClick}
          errorText={""}
          size={"default"}
          extraClass="mb-6"
        />
        <Button
          htmlType="submit"
          type="primary"
          size="medium"
          extraClass="mb-20"
        >
          
        </Button>
      </form>
      <div className="text text_type_main-small mb-4">
        <span> ?</span>
        <Link to="/login">
          <Button htmlType="submit" type="secondary" size="small">
            
          </Button>
        </Link>
      </div>
    </div>
  );
}
